.destacado{

  position: relative;
  display: flex;
flex-wrap: wrap;
  justify-content: space-evenly;
  list-style: none;
  grid-gap: 30px;
}
.destacado li{
  display: flex;
 

}

.destacado img{
 
  border-radius: 3%;
  width: 150%;
}

.des{
  padding-top: 3%;
  padding-bottom: 3%;
  text-align: start;
}


.destacado2{
  position: relative;
  display: flex;
flex-wrap: wrap;
padding-top: 5%;
  justify-content: space-evenly;
  list-style: none;

}

.destacado2 img{
  border-radius: 3%;
  width: 150%;
}

.nav-item:hover{
  background-color: #016b7e9d;
  
}


.home{
  padding-top: 1%;
}

.destacado figure{
  position:relative;
   height: 300px;
   cursor: pointer;
   width: 190px;
   overflow: hidden;
   border-radius: 10px;
   box-shadow: 0px 15px 25px rgba(0,0,0,0.50);
}


.destacado figure img{
  width: 100%;
  height: 100%;
  transition: all 400ms ease-out;
  will-change: transform;
}
.destacado figure .capa{
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  transition: all 400ms ease-out;
  opacity: 0;
  visibility: hidden;
 text-align: center;
}
.destacado figure:hover > .capa {
  opacity: 1;
  visibility: visible;
}
.destacado figure:hover > .capa h3{
  margin-top: 70px;
  margin-bottom: 15px;
}
.destacado figure:hover > img{
  transform: scale(1.3);
}
.destacado figure .capa h3{
  color: #fff;
  font-weight: 400;
  margin-bottom: 120px;
  transition: all 400ms ease-out;
   margin-top: 30px;
}
.destacado figure .capa p{
  color: #fff;
  font-size: 15px;
  line-height: 1.5;
  width: 100%;
  max-width: 220px;
  margin: auto;
}
.star{
